@import url(https://fonts.googleapis.com/css?family=Roboto:500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.app {
  padding: 0 15px; }
  .app .main {
    display: block;
    margin: 0 auto;
    height: 100%;
    max-width: 880px; }

.header__image {
  width: 100%;
  -webkit-filter: grayscale(50%);
          filter: grayscale(50%); }

.navbar {
  display: flex;
  margin: 0 auto 30px;
  max-width: 1080px;
  height: 130px; }
  .navbar__navigation {
    display: flex;
    justify-content: flex-end;
    flex-grow: 1;
    padding-right: 15px; }
    .navbar__navigation__item {
      display: flex;
      flex-flow: column;
      justify-content: center;
      margin-left: 50px; }
      .navbar__navigation__item a {
        font-family: "Open Sans";
        font-size: 18px;
        text-align: center;
        letter-spacing: .3px;
        color: #1a3d6b;
        text-decoration: none; }
        .navbar__navigation__item a:hover {
          color: #060e19;
          border-bottom: 2px solid #1a3d6b; }
  .navbar .logo {
    display: flex;
    align-items: center; }
    .navbar .logo__image {
      height: 70px; }
    .navbar .logo__text {
      padding-left: 10px;
      font-family: "Open Sans";
      font-weight: 600;
      font-size: 24px;
      letter-spacing: .3px;
      color: #193d6b; }

@media screen and (max-width: 783px) {
  .navbar {
    height: 100px;
    margin: 0 10px; }
    .navbar__navigation {
      display: none; }
    .navbar .logo {
      display: flex;
      align-items: center; }
      .navbar .logo__image {
        height: 40px; } }

.about {
  padding: 75px 50px;
  text-align: center; }
  .about__paragraph {
    padding-top: 50px; }
  .about p {
    text-align: left; }

.heading {
  display: inline-block; }
  .heading__text {
    position: relative;
    top: 0;
    font-size: 35px;
    font-family: "Open Sans";
    color: #183c6b;
    margin: 0;
    letter-spacing: .5px; }
    .heading__text:after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 100%;
      margin: 15px auto;
      width: 70px;
      height: 4px;
      background: #183c6b; }

p {
  font-family: "Open Sans";
  color: #4c5157;
  font-size: 24px; }

